import React, {useState, Suspense, lazy} from 'react';
import Animate from 'react-smooth';
import Spinner from '../../spinner/spinner'
import worksData from '../../../libs/worksData';

const Modal = lazy(() => import('../../modal/modal'));
const Works = () => {
    const [isActive, setIsActive] = useState(false);
    const [modalInfo, setModalInfo] = useState({});
    const works = worksData.map((item, i) => {
        const {imgUrl} = item;
        return (
            <div key={i} className="works-item" onClick={(() => openModal(item))}>
                <img src={imgUrl} alt="works"/>
            </div>
        );
    });
    function openModal(item) {
        setIsActive(true);
        setModalInfo(item);
    }
    function closeModal() {
        setIsActive(false);
        setModalInfo({});
    }
    function getIcons(prop) {
        if (prop.title === 'Coffee' || prop.title === 'Todo list') {
            return (
                <div className="works__icons">
                    <img src="https://ucarecdn.com/d1bf20cf-2073-4649-87e6-0a109515201b/react.png" alt="skill_icon"/>
                </div>
            );
        } else {
            return (
                <div className="works__icons">
                    <img src="https://ucarecdn.com/93670477-4578-4816-86c9-d3d787af190c/html.png" alt="skill_icon"/>
                    <img src="https://ucarecdn.com/bbc26da8-ecca-4e47-9533-5169a39b49a9/css.png" alt="skill_icon"/>
                    <img src="https://ucarecdn.com/7c03bc7d-f7f9-40a8-93b3-0c6ded890b23/js.svg" alt="skill_icon"/>
                </div>
            );
        }
    }
    return (
        <Animate to="1" from="0" attributeName="opacity">
            <section className="works">
                <div className="container">
                    <h2 className="works__title title">Works</h2>
                    <div className="works__wrapper">
                        {works}
                    </div>
                </div>
            </section>
            <Suspense fallback={<Spinner/>}>
                <Modal closeModal={closeModal} active={isActive}>
                    <h2 className="modal__title">{modalInfo.title}</h2>
                    <img className="modal__screen" src={modalInfo.screen} alt="works_img"/>
                    <div className="modal__descr">Build with:</div>
                    <div className="modal__footer">
                        {isActive && getIcons(modalInfo)}
                        <div className="modal__links">
                            <a href={modalInfo.githubUrl}><img src="https://ucarecdn.com/4473d8b4-648e-44e0-82a1-8bc8f1582962/github.png" alt="github"/></a>
                            <a href={modalInfo.url} className="modal__link">Live preview</a>
                        </div>
                    </div>
                </Modal>
            </Suspense>
        </Animate>
    );
};

export default Works;